:root {
  --cor-bg: #0f1414;
  --cor-bg-soft: #151b1b;
  --cor-text: #e8e5dc;
  --cor-destaque: #3fbf9a;
  --cor-primaria: #8E1B1B;
  --cor-ouro: #c9a86a;
  --radius: 16px;
  --shadow: 0 10px 30px rgba(0, 0, 0, .25);
  --container: 1120px;
  --gap: 1rem;
  --transition: .2s ease;
}

.theme[data-world="espiritos"] {
  --cor-bg: #0b0f10;
  --cor-bg-soft: #101616;
  --cor-text: #f6f1e4;
  --cor-destaque: #bfe6db;
  --cor-primaria: #3fbf9a;
  --cor-ouro: #e1c58f;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}

body {
  margin: 0;
  font-family: Inter, system-ui, -apple-system, Segoe UI, Roboto, sans-serif;
  color: var(--cor-text);
  background: radial-gradient(1000px 600px at 70% -20%, rgba(143, 27, 27, .15), transparent 60%) var(--cor-bg);
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
}

img {
  max-width: 100%;
  display: block;
}

a {
  color: var(--cor-destaque);
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

a:focus-visible,
button:focus-visible {
  outline: 2px solid var(--cor-destaque);
  outline-offset: 2px;
}

.text--small {
  font-size: .875rem;
  opacity: .85;
}

.container {
  width: min(var(--container), 92%);
  margin-inline: auto;
}

:is(.header__container, .section__container, .footer__container) {
  width: min(var(--container), 92%);
  margin-inline: auto;
}

.btn {
  --btn-bg: var(--cor-primaria);
  --btn-text: #fff;
  border: 0;
  padding: .85rem 1.1rem;
  border-radius: 999px;
  background: var(--btn-bg);
  color: var(--btn-text);
  box-shadow: var(--shadow);
  cursor: pointer;
  transition: transform var(--transition), filter var(--transition), opacity var(--transition);
  display: inline-flex;
  align-items: center;
  gap: .5rem;
}

.btn:hover {
  transform: translateY(-2px);
  filter: brightness(1.05);
}

.btn:active {
  transform: translateY(0);
}

.btn:disabled {
  opacity: .6;
  cursor: not-allowed;
  transform: none;
}

.btn--secundario {
  --btn-bg: transparent;
  --btn-text: var(--cor-text);
  outline: 1px solid rgba(255, 255, 255, .2);
}

.header {
  position: sticky;
  top: 0;
  z-index: 10;
  -webkit-backdrop-filter: saturate(140%) blur(8px);
    backdrop-filter: saturate(140%) blur(8px);
    background: rgba(10, 14, 14, 0.6);
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: .75rem 0;
}

.header__brand {
  display: inline-flex;
  align-items: center;
  gap: .75rem;
}

.header__logo {
  height: 24px;
  image-rendering: -webkit-optimize-contrast;
}

.header__title {
  font-family: Marcellus, serif;
  letter-spacing: .5px;
  text-transform: uppercase;
  opacity: .9;
}

.header__nav {
  display: none;
  gap: 1rem;
}

.header__link {
  font-weight: 600;
}

.header__switch {
  font-weight: 600;
}

@media (min-width: 860px) {
  .header__nav {
    display: flex;
  }
}

.hero {
  position: relative;
  min-height: 82vh;
  display: grid;
  place-items: center;
  text-align: center;
  overflow: clip;
}

.hero__bg {
  position: absolute;
  inset: 0;
  background: url('../images/dogo_onsen.jpg') center/cover no-repeat;
  filter: saturate(0.9) brightness(0.75);
  transform: scale(1.05);
  transition: transform 1.2s ease, filter 1.2s ease;
}

.theme[data-world="espiritos"] .hero__bg {
  transform: scale(1.1);
  filter: saturate(1) brightness(0.8);
}

.hero__container {
  position: relative;
  padding: 5rem 1rem 3rem;
  width: min(980px, 92%);
}

.hero__title {
  font-family: Marcellus, serif;
  font-size: clamp(2.4rem, 5vw + 1rem, 5rem);
  line-height: 1.05;
  letter-spacing: .8px;
  text-shadow: 0 6px 30px rgba(0, 0, 0, .5);
}

.hero__subtitle {
  margin: 1rem auto 2rem;
  max-width: 42ch;
  opacity: .92;
}

.hero__ctas {
  display: flex;
  gap: .75rem;
  justify-content: center;
  flex-wrap: wrap;
}

.section {
  padding-block: clamp(3rem, 5vw, 5rem);
}

.section__title {
  font-family: Marcellus, serif;
  font-size: clamp(1.8rem, 2.5vw + .6rem, 3rem);
  margin: 0 0 1rem;
}

.section__lead {
  max-width: 65ch;
}

.grid {
  display: grid;
  gap: var(--gap);
}

.grid--2 {
  grid-template-columns: 1fr;
}

@media (min-width: 760px) {
  .grid--2 {
    grid-template-columns: repeat(2, 1fr);
  }
}

.card {
  background: linear-gradient(180deg, var(--cor-bg-soft), transparent 120%);
  border: 1px solid rgba(255, 255, 255, .08);
  border-radius: var(--radius);
  padding: 1.2rem 1.2rem 1rem;
  box-shadow: var(--shadow);
}

.card__title {
  margin: 0 0 .5rem;
  font-family: Marcellus, serif;
}

.card__text {
  margin: 0;
}

.section--banho .banho__media {
  margin-top: 1rem;
}

.banho__img {
  border-radius: var(--radius);
  box-shadow: var(--shadow);
}

.personagens__grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: var(--gap);
}

@media (min-width: 860px) {
  .personagens__grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card--personagem .card__figure {
  margin: 0;
}

.card--personagem .card__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 12px;
}

.card--personagem .card__caption {
  text-align: center;
  margin-top: .5rem;
  opacity: .9;
}

.galeria {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: .75rem;
}

@media (min-width: 760px) {
  .galeria {
    grid-template-columns: repeat(3, 1fr);
  }
}

.galeria__item img {
  border-radius: 12px;
  transition: transform .25s ease, filter .25s ease;
  box-shadow: var(--shadow);
}

.galeria__item:hover img {
  transform: translateY(-2px) scale(1.01);
  filter: brightness(1.05);
}

.footer {
  border-top: 1px solid rgba(255, 255, 255, .08);
  padding: 2rem 0;
}

.revelar {
  opacity: 0;
  transform: translateY(14px);
  transition: opacity .7s ease, transform .7s ease;
}

.revelar--in {
  opacity: 1;
  transform: translateY(0);
}

.js .split-line {
  display: inline-block;
  overflow: clip;
}

.js .split-char {
  display: inline-block;
  transform: translateY(1em);
  opacity: 0;
}

.js .split-char.in {
  transform: translateY(0);
  opacity: 1;
  transition: transform .7s cubic-bezier(.2, .8, .2, 1), opacity .7s ease;
}

@media (prefers-reduced-motion: reduce) {

  *,
  *::before,
  *::after {
    animation: none !important;
    transition: none !important;
  }
}